<template>
  <div class="d-flex justify-content-center btn-group" role="group">
      <input type="radio"
      class="btn-check"
      id="takeaway"
      name="mode"
      :value="take"
      @change="$emit('update:mode', $event.target.value)">
      <label class="btn btn-outline-secondary mx-2 flex-fill" for="takeaway">
        <i class="bi bi-shop fs-1"></i>
        <span class="fs-3 d-block">Je choisis à emporter</span>
      </label>
      <input type="radio"
      class="btn-check"
      id="delivery"
      name="mode"
      :value="deliver"
      :disabled="!deliverable"
      @change="$emit('update:mode', $event.target.value)">
      <label class="btn btn-outline-secondary mx-2 flex-fill" for="delivery">
        <i class="bi bi-house fs-1"></i>
        <span class="fs-3 d-block">Je choisis la livraison</span>
      </label>
  </div>
</template>
<script>
import {mapGetters} from 'vuex'
import {takeawayMode, deliveryMode} from './constant.js'

export default {
  name: "select-mode",
  props: {
    mode: {
      type: String,
      default: deliveryMode,
    },
  },
  emits: ["update:mode"],
  data() {
    return {
      take: takeawayMode,
      deliver: deliveryMode,
    }
  },
  computed: {
    ...mapGetters('cart', ['deliverable'])
  },
}
</script>
