<template>
<main class="container">
  <Content/>
  <section class="w-75 mx-auto mb-5" v-if="count">
    <SelectMode v-model:mode="mode" @update:mode="resetMode"/>
    <p class="my-5 text-center" v-if="mode">complétez le formulaire ci-dessous afin que nous puissions continuer la validation de votre commande</p>
    <form class="my-4" v-if="mode" @submit.prevent="">
      <BadAmount v-if="!acceptable && isDelivery"/>
      <PricesInfo v-if="isDelivery && acceptable"/>
      <fieldset>
        <div class="mb-3">
          <label>nom</label>
          <input type="text" id="name" class="form-control" :disabled="!canDeliver" required/>
        </div>
        <div class="mb-3">
          <label>e-mail</label>
          <input type="email" id="email" class="form-control" :disabled="!canDeliver"/>
        </div>
        <div class="mb-3">
          <label>numéro de téléphone</label>
          <input type="text" id="phone" class="form-control" :disabled="!canDeliver"/>
        </div>
      </fieldset>
      <fieldset v-if="isDelivery">
        <div class="mb-3">
          <label>adresse</label>
          <input type="text" id="street" class="form-control" :disabled="!canDeliver" required/>
        </div>
        <div class="row mb-3">
          <div class="col-9">
            <label>ville</label>
            <input type="text" id="city" class="form-control" :disabled="!canDeliver" required v-model="city" @blur="checkDistance"/>
          </div>
          <div class="col">
            <label>pays</label>
            <select class="form-select" v-model="country">
              <option value="Belgique" selected>Belgique</option>
              <option value="France">France</option>
            </select>
          </div>
        </div>
        <div class="mb-3" v-if="distance && city">
          <p class="form-text">distance calculée pour la livraison <span v-if="city && country">à {{city}} ({{country}})</span>: <strong>{{distance.toFixed(2)}}km</strong></p>
        </div>
      </fieldset>
      <div class="mb-3">
        <label>date de retrait/livraison désirée</label>
        <input type="date" :min="when" id="when" class="form-control" v-model="when" :disabled="!canDeliver" required/>
      </div>
      <div class="mb-3 d-flex justify-content-between align-items-center" v-if="isTakeaway || (isDelivery && acceptable)">
        <div class="form-check">
          <input type="checkbox" id="accept" v-model="accepted" class="form-check-input">
          <label for="accept" class="form-check-label">J'accepte <a href="#">les conditions de vente</a></label>
        </div>
        <button type="button" class="btn btn-outline-secondary ms-auto" :disabled="!canSubmit" @click="checkout()">
          <span class="fs-6">Je valide ma commande</span>
        </button>
      </div>
    </form>
  </section>
</main>
</template>
<script>
import {mapGetters, mapMutations, mapActions} from 'vuex'
import Content from '../components/Content.vue'
import SelectMode from '../components/SelectMode.vue'
import PricesInfo from '../components/DeliveryDistancePrice.vue'
import BadAmount from '../components/DeliveryBadAmount.vue'
import {deliveryMode, takeawayMode} from '../components/constant.js'

const contact = {
  name: "",
  phone: "",
  email: "",
  addr: "",
  city: "",
  country: "",
  date: "",
}

export default {
  name: "cart",
  data() {
    let now = new Date()
    now.setDate(now.getDate()+1)
    return {
      mode: "delivery",
      info: contact,
      city: "",
      country: "Belgique",
      confirmed: false,
      accepted: false,
      when: now.toISOString().split('T')[0],
    }
  },
  computed: {
    ...mapGetters("cart", ["count", "deliverable", "acceptable", "total", "distance", "prices"]),
    ...mapGetters("farm", ["position"]),
    canDeliver() {
      if (this.isTakeaway) {
        return true
      }
      return this.isDelivery && this.deliverable
    },
    canSubmit() {
      return this.accepted && this.canDeliver
    },
    isDelivery() {
      return this.mode == deliveryMode
    },
    isTakeaway() {
      return this.mode == takeawayMode
    }
  },
  methods: {
    ...mapActions('cart', ['distanceTo']),
    ...mapMutations('cart', ['setDistance']),
    checkout() {
      console.log("checkout")
    },
    checkDistance() {
      this.distanceTo({city: this.city, country: this.country, position: this.position})
    },
    resetMode() {
      this.setDistance(0)
    }
  },
  components: {
    Content,
    SelectMode,
    PricesInfo,
    BadAmount,
  }
}
</script>
